import React, { useState, useEffect } from "react";
import AppBar from "@material-ui/core/AppBar";
import Button from "@material-ui/core/Button";
import Toolbar from "@material-ui/core/Toolbar";
import useScrollTrigger from "@material-ui/core/useScrollTrigger";
import { makeStyles } from "@material-ui/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import ExitToAppOutlinedIcon from "@material-ui/icons/ExitToAppOutlined";

import logo from "../../assets/logo.svg";
import { Link } from "react-router-dom";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { useTheme } from "@material-ui/core/styles";
import MenuIcon from "@material-ui/icons/Menu";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import IconButton from "@material-ui/core/IconButton";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import { useHistory } from "react-router-dom";

function ElevationScroll(props) {
  const { children } = props;

  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 0,
  });

  return React.cloneElement(children, {
    elevation: trigger ? 4 : 0,
  });
}

const useStyles = makeStyles((theme) => ({
  toolbarMargin: {
    ...theme.mixins.toolbar,
    marginBottom: "3em",
    [theme.breakpoints.down("md")]: {
      marginBottom: "2em",
    },
    [theme.breakpoints.down("xs")]: {
      marginBottom: "1.25em",
    },
  },
  logo: {
    height: "6em",
    [theme.breakpoints.down("md")]: {
      height: "5em",
    },
    [theme.breakpoints.down("xs")]: {
      height: "4.5em",
    },
  },
  tabContainer: {
    margin: "auto",
  },
  tab: {
    // ...theme.typography.tab,  is not working
    fontFamily: "Raleway",
    textTransform: "none",
    fontWeight: "700",
    fontSize: "1rem",
    minWidth: 10,
    marginLeft: "25px",
    color: "white",
  },

  button: {
    fontFamily: "Raleway",
    textTransform: "none",
    fontWeight: "700",
    fontSize: "1rem",
    marginRight: "25px",
  },

  drawerIcon: {
    height: "50px",
    width: "50px ",
  },

  drawerIconContainer: {
    marginLeft: "auto",
    "&:hover": {
      backgroundColor: "transparent",
    },
  },
  drawer: {
    backgroundColor: theme.palette.common.mahmoolBlue,
    width: "178.9px",
    [theme.breakpoints.down("xs")]: {
      width: "161px",
    },
  },

  drawerItem: {
    ...theme.typography.tab,
    color: "white",
    opacity: 0.7,
  },

  draweItemSelected: {
    opacity: 1,
  },
  appbar: {
    zIndex: theme.zIndex.modal + 1,
  },
}));

function Header(props) {
  let history = useHistory();

  const [openDrawer, setOpenDrawer] = useState(false);
  const iOS = process.browser && /iPad|iPhone|iPod/.test(navigator.userAgent);

  const classes = useStyles();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down("md"));
  const [value, setValue] = useState(0);

  const handleChange = (e, newValue) => {
    setValue(newValue);
  };

  const routes = [
    { name: "Orders", link: "/", activeIndex: 0 },
    { name: "Awaiting Location", link: "/waitingl", activeIndex: 1 },
    { name: "Awaiting Reschedule", link: "/waitingr", activeIndex: 2 },
    { name: "Invalid Phone", link: "/invalidp", activeIndex: 3 },
    { name: "All Shipments", link: "/all", activeIndex: 4 },
    { name: "To Return", link: "/return", activeIndex: 5 },
    { name: "Log in", link: "/" },
  ];

  useEffect(() => {
    routes.forEach((route) => {
      switch (window.location.pathname) {
        case `${route.link}`:
          if ((value !== route.activeIndex) && route.name !== "Log in") {
            setValue(route.activeIndex);
          }
          break;
        default:
          break;
      }
    });
  }, [value, routes]);

  const tabs = (
    <React.Fragment>
      <Tabs
        value={value}
        onChange={handleChange}
        className={classes.tabContainer}
        indicatorColor="primary"
      >
        <Tab className={classes.tab} label="Orders" component={Link} to="/" />
        <Tab
          className={classes.tab}
          label="Awaiting Location"
          component={Link}
          to="/waitingl"
        />
        <Tab
          className={classes.tab}
          label="Awaiting Reschedule"
          component={Link}
          to="/waitingr"
        />
        <Tab
          className={classes.tab}
          label="Invalid Phone"
          component={Link}
          to="/invalidp"
        />
        <Tab
          className={classes.tab}
          label="All Shipments"
          component={Link}
          to="/all"
        />
        <Tab
          className={classes.tab}
          label="To Return"
          component={Link}
          to="/return"
        />
      </Tabs>

      <Button
        className={classes.button}
        endIcon={<ExitToAppOutlinedIcon />}
        color="inherit"
        onClick={() => {
          sessionStorage.clear();
          localStorage.clear();
          window.location.href = '/';
        }}
      >
        {" "}
        Log out
      </Button>
    </React.Fragment>
  );

  const drawer = (
    <React.Fragment>
      <SwipeableDrawer
        disableBackdropTransition={!iOS}
        disableDiscovery={iOS}
        open={openDrawer}
        onClose={() => setOpenDrawer(false)}
        onOpen={() => setOpenDrawer(true)}
        classes={{
          paper: classes.drawer,
        }}
      >
        <di className={classes.toolbarMargin} />
        <List disablePadding>
          <ListItem
            className={
              value === 0
                ? [classes.drawerItem, classes.draweItemSelected]
                : classes.drawerItem
            }
            onClick={() => {
              setOpenDrawer(false);
              setValue(0);
            }}
            divider
            button
            component={Link}
            to="/"
            selected={value === 0}
          >
            <ListItemText disableTypography>RFD</ListItemText>
          </ListItem>

          <ListItem
            className={
              value === 1
                ? [classes.drawerItem, classes.draweItemSelected]
                : classes.drawerItem
            }
            onClick={() => {
              setOpenDrawer(false);
              setValue(1);
            }}
            divider
            button
            component={Link}
            to="/waitingl"
            selected={value === 1}
          >
            <ListItemText disableTypography>Awaiting Location</ListItemText>
          </ListItem>

          <ListItem
            className={
              value === 2
                ? [classes.drawerItem, classes.draweItemSelected]
                : classes.drawerItem
            }
            onClick={() => {
              setOpenDrawer(false);
              setValue(2);
            }}
            divider
            button
            component={Link}
            to="/waitingr"
            selected={value === 2}
          >
            <ListItemText disableTypography>Awaiting Reschedule </ListItemText>
          </ListItem>

          <ListItem
            className={
              value === 3
                ? [classes.drawerItem, classes.draweItemSelected]
                : classes.drawerItem
            }
            onClick={() => {
              setOpenDrawer(false);
              setValue(3);
            }}
            divider
            button
            component={Link}
            to="/invalidp"
            selected={value === 3}
          >
            <ListItemText disableTypography>Invalid Phone</ListItemText>
          </ListItem>

          <ListItem
            className={
              value === 4
                ? [classes.drawerItem, classes.draweItemSelected]
                : classes.drawerItem
            }
            onClick={() => {
              setOpenDrawer(false);
              setValue(4);
            }}
            divider
            button
            component={Link}
            to="/all"
            selected={value === 4}
          >
            <ListItemText disableTypography>All Shipments</ListItemText>
          </ListItem>

          <ListItem
            className={
              value === 4
                ? [classes.drawerItem, classes.draweItemSelected]
                : classes.drawerItem
            }
            onClick={() => {
              setOpenDrawer(false);
              setValue(4);
            }}
            divider
            button
            component={Link}
            to="/return"
            selected={value === 4}
          >
            <ListItemText disableTypography>To Return</ListItemText>
          </ListItem>

          <ListItem
            className={classes.drawerItem}
            onClick={() => setOpenDrawer(false)}
            divider
            button
            component={Link}
            to="/"
            selected={value === 5}
          >
            <Button
              className={classes.button}
              endIcon={<ExitToAppOutlinedIcon />}
              color="inherit"
              onClick={() => {
                sessionStorage.clear();
                localStorage.clear();
                window.location.href = '/';
              }}
            >
              {" "}
              Log out
            </Button>
          </ListItem>
        </List>
      </SwipeableDrawer>
      <IconButton
        className={classes.drawerIconContainer}
        onClick={() => setOpenDrawer(!openDrawer)}
        disableRipple
      >
        <MenuIcon className={classes.drawerIcon} />
      </IconButton>
    </React.Fragment>
  );
  return (
    <React.Fragment>
      <ElevationScroll>
        <AppBar className={classes.appbar}>
          <Toolbar disableGutters>
            <img src={logo} className={classes.logo} alt="mahmool logo" />
            {matches ? drawer : tabs}
          </Toolbar>
        </AppBar>
      </ElevationScroll>
      <div className={classes.toolbarMargin} />
    </React.Fragment>
  );
}

export default Header;