import React from "react";
import { ThemeProvider } from "@material-ui/core/styles";
import { BrowserRouter, Route, Switch } from "react-router-dom";

import theme from "./UI/Theme.js";
import Header from "./UI/Header.js";
import Signup from "./Signup.js";


import InvalidPhone from "./tables/invalidPhoneTable.js";
import InvalidCity from "./tables/invalidCityTable.js";
import WaitingLocation from "./tables/waitingLocationTable.js";
import WaitingReschedule from "./tables/waitingRescheduleTable.js";
import OrdersTable from "./tables/ordersTable";
import ToReturn from "./tables/toReturnTable.js";

function Dashboard() {
  return (
    <ThemeProvider theme={theme}>
      <BrowserRouter>
        <Header />
        <Switch>
          <Route exact path="/">
            {" "}
            <OrdersTable />{" "}
          </Route>

          

          <Route exact path="/waitingl">
            {" "}
            <WaitingLocation />{" "}
          </Route>
          <Route exact path="/waitingr">
            {" "}
            <WaitingReschedule />{" "}
          </Route>

          <Route exact path="/invalidp">
            {" "}
            <InvalidPhone />{" "}
          </Route>

          <Route exact path="/all">
            {" "}
            <InvalidCity />{" "}
          </Route>

          <Route exact path="/return">
            {" "}
            <ToReturn />{" "}
          </Route>

          <Route exact path="/signup">
            {" "}
            <Signup />{" "}
          </Route>
        </Switch>
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default Dashboard;